$icomoon-font-family: "icomoon";
$icomoon-font-path: "../fonts/icomoon" !default;

$icon-twitter: "\e906";
$icon-apple: "\e907";
$icon-arrow-down: "\e908";
$icon-arrow-left: "\e909";
$icon-add: "\e90a";
$icon-close: "\e90b";
$icon-arrow-dropdown: "\e90c";
$icon-share: "\e90d";
$icon-language: "\e90e";
$icon-download: "\e90f";
$icon-calendar: "\e910";
$icon-person: "\e911";
$icon-folder: "\e912";
$icon-log-out: "\e913";
$icon-password: "\e914";
$icon-pencil: "\e915";
$icon-hide: "\e916";
$icon-show: "\e917";
$icon-warning: "\e918";
$icon-facebook: "\e919";

@font-face {
  font-family: $icomoon-font-family;
  src:
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?4a5sly') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?4a5sly') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?4a5sly##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: $icomoon-font-family !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


