.custom-checkbox-label {
  position: relative;
  display: inline-block;
  vertical-align: top;
  align-items: center;
  margin: 0;
  width: 24px;
  height: 24px;
  flex: 0 0 24px;
  user-select: none;
  cursor: pointer;
}

.custom-checkbox-label .common-checkbox:checked ~ .custom-checkbox {
  border-color: #00ACCD;
}

.custom-checkbox-label .common-checkbox:checked ~ .custom-checkbox:after {
  display: block;
  border-color: #00ACCD;
}

.custom-checkbox-label__active .custom-checkbox {
  border-color: #00ACCD;
