  display: block;
  border-color: #00ACCD;
}

.common-checkbox {
  width: 0;
  height: 0;
  margin: 0;
  opacity: 0;
}

.custom-checkbox {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: solid 1px #9EB3B9;
  background-color: #fff;
  border-radius: 2px;
}

.custom-checkbox:after {
  content: "";
  position: absolute;
  display: none;
  left: 50%;
  top: 45%;
  width: 8px;
  height: 13px;
  border: solid #00ACCD;
  border-width: 0 2px 2px 0;
  transform: translate(-50%, -50%) rotate(45deg);
}

.custom-after {
  position: absolute;
  display: none;
  left: 50%;
  top: 45%;
  width: 8px;
  height: 13px;
  border: solid #00ACCD;
  border-width: 0 2px 2px 0;
  transform: translate(-50%, -50%) rotate(45deg);
}

